import React from 'react';
import Logo from './Logo';
import Summary from './Summary';
import Certifications from './Certifications';
import Contact from './Contact';

function Home() {
  return (
    <div className="home-container">
      <Logo />
      <div className="container">
        
        <Summary />

        <Certifications />

        <Contact />
      </div>
    </div>
  );
}

export default Home;
